<script>
export default {
  data() {
    return {
      radio: "0", //性别
      username: "",
      numberValidateForm: {
        age: null
      },
      cities: [
        {
          value: "Beijing",
          label: "北京"
        },
        { value: "Shanghai", label: "上海" },
        { value: "Tianjin", label: "天津" },
        { value: "Jiangsu", label: "江苏" },
        { value: "Zhejiang", label: "浙江" },
        { value: "Anhui", label: "安徽" },
        { value: "Fujian", label: "福建" },
        { value: "Jiangxi", label: "江西" },
        { value: "Hunan", label: "湖南" },
        { value: "Shandong", label: "山东" },
        { value: "Henan", label: "河南" },
        { value: "Neimeng", label: "内蒙古自治区" },
        { value: "Hubei", label: "湖北" },
        { value: "Ningxia", label: "宁夏回族自治区" },
        { value: "Guangdong", label: "广东" },
        { value: "Xizang", label: "西藏自治区" },
        { value: "Hainan", label: "海南" },
        { value: "Guangxi", label: "广西壮族自治区" },
        { value: "Sichuan", label: "四川" },
        { value: "Hebei", label: "河北" },
        { value: "Guizhou", label: "贵州" },
        { value: "Chongqing", label: "重庆" },
        { value: "Shanxi", label: "山西" },
        { value: "Yunnan", label: "云南" },
        { value: "Liaoning", label: "辽宁" },
        { value: " Shanxi", label: "陕西" },
        { value: "Jilin", label: "吉林" },
        { value: "Gansu", label: "甘肃" },
        { value: "Heilongjiang", label: "黑龙江" },
        { value: "Qinghai", label: "青海" },
        { value: "Taiwan", label: "台湾" },
        { value: "Xianggang", label: "香港特别行政区" },
        { value: "Aomen", label: "澳门特别行政区" }
      ],
      value6: "湖南",
      dynamicValidateForm: {
        domains: [
          {
            value: ""
          }
        ],
        email: ""
      },
      imageUrl: "",
      modifyUsername: true,
      text: "",
      headers: "JWT " + localStorage.getItem("token")
    };
  },
  methods:{
    handleAvatarSuccess(res, file) {
        this.imageUrl = res.data.file_data
    },
    beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/png' || file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 或者 PNG格式!');
            }
            if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        submissionMsg(){
            var reg = new RegExp("^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$")
            var sex = ''
            if(this.radio == '0'){
                sex = '保密'
            }else if(this.radio == '1'){
                sex = '男'
            }else{
                sex = '女'
            }

            if(!reg.test(this.dynamicValidateForm.email)){
                alert('邮箱格式不对，请输入正确的邮箱！')
            }else{
                ajax.modifyMsg(this.people["username"],sex,this.value6,this.text,this.imageUrl,this.dynamicValidateForm.email).then((resp)=>{
                    if(resp.data.code == 1){
                        alert('修改信息成功')
                        var data = {
                            "username": resp.data.data.user_info.username,
                            "email": resp.data.data.user_info.email,
                            "gender": resp.data.data.user_info.gender,        //性别
                            "scores": resp.data.data.user_info.scores,        //用户积分
                            "signature": resp.data.data.user_info.signature,  //个性签名
                            "city": resp.data.data.user_info.city,
                            "avatar": resp.data.data.user_info.avatar         //头像
                        }   
                        this.modifypeoplemsg(JSON.stringify(data))
                    }
                })
                this.$router.push('/mine')
            }
        },
  },
};
</script>


